		methods: {
			// swiper提示数字
			currentChange (e) {
				this.prompt.index = e.detail.current + 1;
			},
			// 点击swiper
			currentClick (index) {
				uni.navigateTo({
					url: '/pages/detailShow/detailShow?index=' + encodeURIComponent(JSON.stringify(index))
				});
			},
			// 点击了限时秒杀。 
			TimeLimitClick (content) {
				uni.navigateTo({
					url: '/pages/detailShow/detailShow?content=' + encodeURIComponent(JSON.stringify(content))
				});
			},
			// 点击了精品团购
			dealsClick (title) {
				uni.navigateTo({
					url: 'TimeLimit/TimeLimit?content=' + encodeURIComponent(JSON.stringify(title))
				});
			}
 		},
		// 监听点击导航栏按钮
		onNavigationBarButtonTap (e) {
			if (e.index == 0) {
				this.$api.msg('点击了扫描');
			} else if (e.index == 1) {
				this.$api.msg('点击了消息');
				uni.navigateTo({
					url: '/pages/home/messages/messages'
				});
			}
		},
		// 监听点击导航栏搜索框
		onNavigationBarSearchInputClicked () {
			this.$api.msg('点击了搜索框');
		}
